'use client';

import { useState } from 'react';
import { useSearchParams } from 'next/navigation';

const fields = [
  { id: 'name', label: 'Name', type: 'text', required: true },
  { id: 'email', label: 'Email', type: 'email', required: true },
  { id: 'business', label: 'Business Name (Optional)', type: 'text' },
  { id: 'industry', label: 'Business Type', type: 'text', required: true },
  { id: 'timeline', label: 'Preferred Timeline', type: 'text' },
];

const inputStyles =
  'w-full border-4 border-black px-4 py-3 text-lg bg-transparent placeholder-black font-mono focus:outline-none focus:bg-black focus:text-white transition-colors';

export default function ContactForm() {
  const [submitted, setSubmitted] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const params = useSearchParams();
  const tier = params.get('tier') || '';

  const handleSubmit = async (e) => {
    e.preventDefault();
    const form = e.target;
    const data = new FormData(form);

    setLoading(true);
    setError(null);
    window.umami?.track('form_submit_started');

    try {
      const res = await fetch('https://formspree.io/f/mrbqowkn', {
        method: 'POST',
        body: data,
        headers: { Accept: 'application/json' },
      });

      setLoading(false);
      if (res.ok) {
        setSubmitted(true);
        form.reset();
        window.umami?.track('form_submit_contact');
      } else {
        setError('Something went wrong. Please try again later.');
        window.umami?.track('form_submit_error');
      }
    } catch {
      setLoading(false);
      setError('Network error. Please try again later.');
      window.umami?.track('form_submit_network_error');
    }
  };

  if (submitted) {
    return (
      <div className='text-2xl text-center font-bold text-black border-t-4 border-black pt-12'>
        Thank you for reaching out! I’ll get back to you soon.
      </div>
    );
  }

  return (
    <form onSubmit={handleSubmit} className='space-y-8 border-t-4 border-black pt-10'>
      <div className='grid grid-cols-1 md:grid-cols-2 gap-8'>
        {fields.map(({ id, label, type, required }) => (
          <div key={id} className='flex flex-col'>
            <label htmlFor={id} className='text-xl font-semibold mb-2'>
              {label}
            </label>
            <input
              id={id}
              name={id}
              type={type}
              placeholder={label}
              required={required}
              className={inputStyles}
            />
          </div>
        ))}
      </div>

      <input type='hidden' name='package' value={tier} />
      <input type='hidden' name='ref' value='landing-pages' />
      <input type='hidden' name='subject' value='Landing Page Inquiry' />

      <div className='flex flex-col'>
        <label htmlFor='message' className='text-xl font-semibold mb-2'>
          Message
        </label>
        <textarea
          id='message'
          name='message'
          rows='6'
          placeholder='Tell me what you’re looking for! Services, style, colors, must-haves, etc.'
          className={inputStyles}
          required
        />
      </div>

      {error && <div className='text-red-600 text-lg font-bold'>{error}</div>}

      <button
        type='submit'
        disabled={loading}
        className={`w-full border-4 border-black p-4 text-xl font-bold uppercase tracking-wide transition-colors ${
          loading ? 'bg-gray-400 text-white' : 'bg-black text-white hover:bg-white hover:text-black'
        }`}>
        {loading ? 'Sending...' : 'Send Message'}
      </button>
    </form>
  );
}
